<template>
    <div class="road" oncontextmenu="self.event.returnValue=false" v-if="list!='list'">

        <div class="roadLeft">
            <div class="roadItem" v-for="(item,index) in list.attribute.ordinary" :key="index">
                <div class="routes" v-if="list.routes[index]">
                    <div class="route" v-for="(route,routeIndex) in list.routes[index]" :key="routeIndex" @mouseover="showRouteDetail(route,index)" @mouseout="hideRouteDetail"></div>
                </div>
                <div class="name" @mouseover="showDetail(index)" @mouseout="hideDetail">{{list.name}}{{index + 1}}{{'\u6bb5'}}</div>
            </div>
        </div>

        <div class="roadRight" v-if="presentIndex!==''">
            <div class="detail" v-show="detailIsShow">
                <p>{{list.name}}{{presentIndex + 1}}{{'\u6bb5'}}</p>
                <p class="ordinary" v-for="(ordinary,ordinaryIndex) in list.attributeName.ordinary" :key="ordinaryIndex">
                    {{ordinary}}{{list.attribute.ordinary[presentIndex][ordinaryIndex]}}
                </p>
                <p class="peculiar" v-for="attribute in attributeDetail" :key="attribute">
                    {{attribute}}
                </p>
            </div>

            <div class="routeDetail" v-show="RouteDetailIsShow">
                <p>{{ list.name }}{{ presentIndex + 1 + '\u6bb5' }} <b>↓</b></p>
                <p>{{ list.name }}{{ presentIndex + 2 + '\u6bb5' }}</p>
                <p v-for="(Route,RouteIndex) in RouteDetail" :key="RouteIndex">
                    {{ list.material[RouteIndex] }}：<b>{{ Route }}</b>
                </p>
            </div>
        </div>

    </div>
</template>

<script>

export default {
    name: "Road",
    data() {
        return {
            list: 'list',
            presentIndex: '',
            attributeDetail: '',
            RouteDetail: '',
            detailIsShow: false,
            RouteDetailIsShow: false
        }
    },
    created() {

        let path = Object.keys(this.$route.query)[0];

        let xhr = new XMLHttpRequest();
        let that = this;
        xhr.open('get', `https://jiusi.tk/data/${path}.json`);
        xhr.send();
        xhr.onreadystatechange = function () {
            if (xhr.status == 200 && xhr.readyState == 4) {
                that.list = JSON.parse(xhr.response);
            }
        }
    },
    methods: {
        showDetail(index) {
            this.detailIsShow = true;
            this.presentIndex = index;
            let peculiar = this.list.attributeName.peculiar.split('[]');
            for (let i = 0; i < this.list.attribute.peculiar[index].length; i++) {
                peculiar[i] = peculiar[i] + this.list.attribute.ordinary[index][i]
            }
            this.attributeDetail = peculiar.join('').split('br');
        },
        hideDetail() {
            this.detailIsShow = false;
        },
        showRouteDetail(data, index) {
            this.RouteDetailIsShow = true;
            this.presentIndex = index;
            this.RouteDetail = data;
        },
        hideRouteDetail() {
            this.RouteDetailIsShow = false;
        }
    }
}
</script>

<style>
.road {
    display: flex;
    box-sizing: border-box;
    margin: auto;
    width: 100vw;
    max-width: 100%;
    user-select: none;
    -webkit-tap-highlight-color: rgba(255, 255, 255, 0);
    -webkit-user-select: none;
    -moz-user-focus: none;
    -moz-user-select: none;
}

.roadLeft {
    display: flex;
    padding: 50px 0;
    width: 100%;
    flex-direction: column-reverse;
    justify-content: center;
    align-items: center;
}

.roadRight {
    position: absolute;
    top: 20vh;
    right: calc(50vw - 500px);
    width: 400px;
}

.name {
    display: inline-block;
    margin: 0 10px 10px;
    padding: 10px 20px;
    border-radius: 5px;
    background: #fee4cb;
    color: #ff942e;
    cursor: pointer;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}

.name:hover {
    background: #ff942e;
    color: #fff;
}

.routes {
    display: flex;
    margin: auto;
    width: 40%;
    justify-content: space-evenly;
}

.route {
    position: relative;
    width: 6px;
    height: 30px;
    background: #c8f7dc;
    cursor: pointer;
}

.route:before {
    position: absolute;
    top: -16px;
    left: -5px;
    border: 8px solid transparent;
    content: "";
    border-bottom-color: #c8f7dc;
}

.route:hover {
    background: #34c471;
}

.route:hover:before {
    border-bottom-color: #34c471;
}

.detail,
.routeDetail {
    padding: 15px 10px;
    border-radius: 10px;
    background: white;
}

/* .detail {
    background: #fee4cb;
}

.routeDetail {
    background: #c8f7dc;
} */

.detail p:nth-of-type(1) {
    color: #ff942e;
    font-weight: 800;
}

.detail .ordinary {
    font-size: 0.9em;
    color: #ff942e;
}

.detail .peculiar {
    color: #34c471;
    font-size: 0.8em;
}

.routeDetail p:nth-of-type(1) b {
    color: #ff942e;
}

.routeDetail p:nth-of-type(n + 3) b {
    color: #34c471;
}

.routeDetail p:nth-of-type(1),
.routeDetail p:nth-of-type(2) {
    color: #34c471;
    font-weight: 800;
}

.routeDetail p:nth-of-type(n + 3) {
    font-size: 0.8em;
    color: #ff942e;
}

@media screen and (max-width: 800px) {
    .roadLeft {
        width: 150px;
    }

    .roadRight {
        right: 28px;
        width: 50vw;
    }
}
</style>
